body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-box {
    position: absolute;
    top: 300px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 80%;
    height: 50px;
    /* add your search box styles here */
}

.custom-popup_overlay__3FaJY {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms;
}
.custom-popup_popup__1Z5J2 {
    margin: 100px auto;
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    width: 55%;
    position: relative;
    transition: all 5s ease-in-out;
}

.custom-popup_popup__1Z5J2 h2 {
    margin-top: 0;
    color: #333;
    font-family: Roboto, "Roboto Light", sans-serif;
}
.custom-popup_popup__1Z5J2 .custom-popup_close__39YZK {
    position: absolute;
    top: 20px;
    right: 30px;
    transition: all 200ms;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    color: #333;
}

.custom-popup_popup__1Z5J2 .custom-popup_content__14ZbI {
    max-height: 30%;
    overflow: auto;
}

@media screen and (max-width: 700px) {
    .custom-popup_popup__1Z5J2 {
        width: 70%;
    }
}
